import React, {useState} from "react";
import lodash from "lodash";
import request, {getRequestParams} from '../request/api'

const Home = React.memo(() => {
    const [articles, setArticles] = useState([]);
    const [pages, setPages] = useState([]);
    const [pagination, setPagination] = useState({
        current: 1,
        pageSize: 20,
        total: 0,
    })
    const [favoritedisabled, setFavoritedisabled] = useState(false)

    function getArticles(pageIndex) {
        let offset = pageIndex === null ? 0: (pageIndex - 1) * 20;
        let newPagination = {...pagination};
        newPagination.current = pageIndex === null ? 1: pageIndex;
        request({
            ...getRequestParams("articles", null),
            params: {
                offset: offset,
                limit: 20
            }
        }).then(res => {
            // console.log(res)
            const fetchedArticles = res.data.articles;
            const articlesCount = res.data.articlesCount;
            newPagination.total = articlesCount;
            const newPages = [];
            for (let i = 0; i < articlesCount / 20; i++) {
                newPages.push(i);
            }
            if (!lodash.isEqual(pages, newPages)) {
                setPages(newPages);
            }
            if (!lodash.isEqual(articles, fetchedArticles)) {
                setArticles(fetchedArticles)
            }
            if (!lodash.isEqual(pagination, newPagination) && pageIndex !== null) {
                setPagination(newPagination);
            }
        }).catch(err => console.log(err));
    }
    getArticles(null);

    function onPageClick(pageIndex) {
        console.log("Start to switch page ", pageIndex);
        getArticles(pageIndex);
    }

    function onFnFavorite(obj, index) {
        request({
            ...getRequestParams("favorite", obj),
        }).then(res => {
            console.log("favorite", res);
            const newArticles = [...articles];
            newArticles[index] = res.data.article;
            setArticles(newArticles)
            setFavoritedisabled(false);
        }).catch(err => console.log(err));
        setFavoritedisabled(true);
    }
    // console.log("articles===============: ", articles);
    // console.log("pagination===============: ", pagination);

    return (
        <div className="home-page">
            <div className="banner">
                <div className="container">
                    <h1 className="logo-font">conduit</h1>
                    <p>A place to share your knowledge.</p>
                </div>
            </div>

            <div className="container page">
                <div className="row">
                    <div className="col-md-9">
                        <div className="feed-toggle">
                            <ul className="nav nav-pills outline-active">
                                <li className="nav-item">
                                    <a className="nav-link" href="">Your Feed</a>
                                </li>
                                <li className="nav-item">
                                    <a className="nav-link active" href="">Global Feed</a>
                                </li>
                            </ul>
                        </div>
                        {
                            articles.map((item, index) => {
                                return (<div className="article-preview" key={index}>
                                    <div className="article-meta">
                                        <a href="/profile/eric-simons"><img src={item.author.image}/></a>
                                        <div className="info">
                                            <a href="/profile/eric-simons" className="author">{item.author.username}</a>
                                            <span className="date">{item.createdAt}</span>
                                        </div>
                                        <button
                                            className="btn btn-outline-primary btn-sm pull-xs-right"
                                            onClick={() => onFnFavorite(item, index)}
                                            disabled={favoritedisabled}
                                        >
                                            <i className="ion-heart"></i> {item.favoritesCount}
                                        </button>
                                    </div>
                                    <a href="/article/how-to-build-webapps-that-scale" className="preview-link">
                                        <h1>{item.title}</h1>
                                        <p>{item.description}</p>
                                        <span>Read more...</span>
                                        <ul className="tag-list">
                                            <li className="tag-default tag-pill tag-outline">realworld</li>
                                            <li className="tag-default tag-pill tag-outline">implementations</li>
                                        </ul>
                                    </a>
                                </div>)
                            })
                        }

                        <ul className="pagination">
                            {
                                pages.length === 0 ? (
                                    <li className="page-item active" key='1'>
                                        {/*<a className="page-link" href="" onClick={() => onPageClick(1)}>1</a>*/}
                                        <button className="page-link" onClick={() => onPageClick(1)}>1</button>
                                    </li>
                                ) : pages.map((item, index) => {
                                    // console.log("page: ", pagination.current, item, index);
                                    return (
                                        <li className={pagination.current === item + 1? "page-item active" : "page-item"} key={index}>
                                            {/*<a className="page-link" href="" onClick={() => onPageClick(index + 1)}>{index + 1}</a>*/}
                                            <button className="page-link" onClick={() => onPageClick(item + 1)}>{item + 1}</button>
                                        </li>
                                    )
                                })
                            }
                        </ul>
                    </div>

                    <div className="col-md-3">
                        <div className="sidebar">
                            <p>Popular Tags</p>

                            <div className="tag-list">
                                <a href="" className="tag-pill tag-default">programming</a>
                                <a href="" className="tag-pill tag-default">javascript</a>
                                <a href="" className="tag-pill tag-default">emberjs</a>
                                <a href="" className="tag-pill tag-default">angularjs</a>
                                <a href="" className="tag-pill tag-default">react</a>
                                <a href="" className="tag-pill tag-default">mean</a>
                                <a href="" className="tag-pill tag-default">node</a>
                                <a href="" className="tag-pill tag-default">rails</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
});

export default Home;