import {Link, NavLink} from "react-router-dom";
import {getToken} from '../utils/cookie'

function Heads() {
    if (getToken()) {
        return (
            <nav className="navbar navbar-light">
                <div className="container">
                    <a className="navbar-brand" href="index.html">conduit</a>
                    <ul className="nav navbar-nav pull-xs-right">
                        <li className="nav-item">
                            <NavLink className="nav-link" to="/">Home</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className="nav-link" to="/editor" activeClassName="active">
                                <i className="ion-compose"></i>&nbsp;New Post
                            </NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className="nav-link" to="/setting">
                                <i className="ion-gear-a"></i>&nbsp;Settings
                            </NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className="nav-link" to="/article">Article</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className="nav-link ng-binding" to="/profile" ui-sref-active="active"
                                     ui-sref="app.profile.main({ username: $ctrl.currentUser.username })">
                                <img ng-src="https://s21.ax1x.com/2024/07/14/pk5iHPJ.png" className="user-pic"
                                     src="https://s21.ax1x.com/2024/07/14/pk5iHPJ.png"/>
                                Eric Simons
                            </NavLink>
                        </li>
                    </ul>
                </div>
            </nav>
        )
    }
    return (
        <nav className="navbar navbar-light">
            <div className="container">
                <a className="navbar-brand" href="index.html">conduit</a>
                <ul className="nav navbar-nav pull-xs-right">
                    <li className="nav-item">
                        <NavLink className="nav-link" to="/">Home</NavLink>
                    </li>
                    <li className="nav-item">
                        <NavLink className="nav-link" to="/login">Sign in</NavLink>
                    </li>
                    <li className="nav-item">
                        <NavLink className="nav-link" to="/register">Sign up</NavLink>
                    </li>
                </ul>
            </div>
        </nav>
    );
}

export default Heads;