import {useEffect, useState} from 'react';
import {useLocation, useNavigate} from "react-router-dom";
import request from '../request/api';
import {setToken} from '../utils/cookie'
import {getRequestParams} from '../request/api'

const Login = () => {
    const [data, setData] = useState({
        username: "",
        email: "111",
        password: "111",
    })
    const location = useLocation();
    const navigate = useNavigate();

    function isRegister() {
        return location.pathname === '/register';
    }

    function submit() {
        console.log("submit....")
        if (data.email !== "" && data.email.indexOf("@") !== -1 && data.password !== "") {
            console.log("=======Start to login....")
            const user = {
                "username": data.username,
                "email": data.email,
                "password": data.password,
            };
            const commonParams = isRegister()? getRequestParams("register", null) : getRequestParams("login", null)
            request({
                ...commonParams,
                data: {user}
            }).then((res) => {
                console.log(res);
                setToken(res.data.user.token);
                navigate("/", {replace: false});
            }).catch((err) => console.log(err));
        }
    }

    function UserName() {
        return (
            <fieldset className="form-group">
                <input className="form-control form-control-lg" type="text" placeholder={data.username} onBlur={(event) => {
                    setData({...data, username: event.target.value});
                }} />
            </fieldset>
        )
    }

    return (
        <div className="auth-page">
            <div className="container page">
                <div className="row">
                    <div className="col-md-6 offset-md-3 col-xs-12">
                        <h1 className="text-xs-center">
                            {!isRegister()? "Sign in" : "Sign up"}
                        </h1>
                        <p className="text-xs-center">
                            <a href="/register">
                                {!isRegister()? "Need an account?" : "Have an account?"}
                            </a>
                        </p>

                        <ul className="error-messages">
                            <li>That email is already taken</li>
                        </ul>

                        <form onSubmit={submit}>
                            {
                                isRegister() && <UserName />
                            }
                            <fieldset className="form-group">
                                <input className="form-control form-control-lg" type="text" placeholder={data.email} onBlur={(event) => {
                                    setData({...data, email: event.target.value});
                                }} />
                            </fieldset>
                            <fieldset className="form-group">
                                <input className="form-control form-control-lg" type="password" placeholder={data.password} onBlur={(event) => {
                                    setData({...data, password: event.target.value});
                                }}/>
                            </fieldset>
                            <button className="btn btn-lg btn-primary pull-xs-right">
                                {!isRegister()? "Sign in" : "Sign up"}
                            </button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Login;