import {createHashRouter} from "react-router-dom";
import Home from "../Home";
import Article from "../Article";
import Login from "../Login";
import Setting from "../Setting";
import Profile from "../Profile";
import Editor from "../Editor";
import Heads from "../Heads";
import Foots from "../Foots";
import {getToken} from '../utils/cookie'

const elementPackage = function (children) {
    return (
        <div>
            <Heads/>
            {children}
            <Foots/>
        </div>
    )
}

let router;
if (getToken()) {
    router = createHashRouter([
        {
            path: "/",
            exact: true,
            element: elementPackage(<Home/>),
        },
        {
            path: "/editor",
            exact: true,
            element: elementPackage(<Editor/>),
        },
        {
            path: "/setting",
            exact: true,
            element: elementPackage(<Setting/>),
        },
        {
            path: "/profile",
            exact: true,
            element: elementPackage(<Profile/>),
        },
        {
            path: "/article",
            exact: true,
            element: elementPackage(<Article/>),
        },
        {
            path: "/login",
            exact: true,
            element: elementPackage(<Home/>),
        },
        {
            path: "/register",
            exact: true,
            element: elementPackage(<Home/>),
        }
    ]);
}
else {
    router = createHashRouter([
        {
            path: "/",
            exact: true,
            element: elementPackage(<Login/>),
        },
        {
            path: "/editor",
            exact: true,
            element: elementPackage(<Login/>),
        },
        {
            path: "/setting",
            exact: true,
            element: elementPackage(<Login/>),
        },
        {
            path: "/profile",
            exact: true,
            element: elementPackage(<Login/>),
        },
        {
            path: "/login",
            exact: true,
            element: elementPackage(<Login/>),
        },
        {
            path: "/register",
            exact: true,
            element: elementPackage(<Login/>),
        }
    ]);
}

export default router;
