import axios from "axios";
import Cookie from "js-cookie";

const request = axios.create({
    // baseURL: "https://conduit.productionready.io"
    baseURL: "https://api.realworld.io"
});

if (Cookie.get("auth")) {
    request.interceptors.request.use(function (config) {
        // 添加token 请求头中添加
        config.headers.Authorization = `Token ${Cookie.get("auth")}`
        return config;
    }, function (error) {
        // Do something with request error
        return Promise.reject(error);
    });
}

export function getRequestParams(arg, obj) {
    switch (arg) {
        case "login":
            return {
                method: "POST",
                url: "/api/users/login"
            }
        case "register":
            return {
                method: "POST",
                url: "/api/users",
            }
        case "articles":
            return {
                method: "GET",
                url: "/api/articles",
            }
        case "favorite":
            console.log("article", obj);
            const url = `/api/articles/${obj.slug}/favorite`;
            if (!obj.favorited) {
                console.log("action: add favorite")
                return {
                    method: "POST",
                    url: url,
                }
            }
            else {
                console.log("action: delete favorite")
                return {
                    method: "DELETE",
                    url: url,
                }
            }
    }
}

export default request;
